.headerNav img.brand {
  width: 36px;
  height: 34px;
}
.headerNav .search-bar {
  display: flex;
  align-items: center;
  padding: 8px 16px;
  background: #efeff4;
}
.headerNav .search-bar span {
  height: 32px;
  line-height: 32px;
  display: inline-block;
  vertical-align: middle;
}
.headerNav .search-bar .input-container {
  flex: 1 1;
}
.headerNav .search-bar .input {
  width: 100%;
  border-radius: 3px;
  font-size: 14px;
  background: #fff;
  text-align: left;
}
.headerNav .search-bar .input .am-icon {
  float: left;
  height: 32px;
  margin-left: 5px;
}
.headerNav .search-bar .input span {
  margin-left: 12px;
}
.headerNav .search-bar .filter-trigger {
  flex: 0 1 auto;
  font-size: 14px;
  margin-left: 12px;
}
.headerNav .search-bar .filter-trigger .iconfont {
  font-size: 24px;
}
