.sidebar-content {
  box-sizing: border-box;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.sidebar-content .filters {
  flex: 1 1;
  overflow-y: auto;
  padding: 12px;
}
.sidebar-content .btn-groups {
  flex: 0 1 40px;
  padding: 12px;
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100%;
}
.sidebar-content .btn-groups .am-button {
  width: 36%;
  max-width: 136px;
  font-size: 16px;
  box-sizing: border-box;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
}
.sidebar-content .btn-groups .am-button:before {
  border-radius: 40px;
}
.sidebar-content .areas-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.sidebar-content .areas-list .am-button {
  margin-right: 5px;
  margin-bottom: 5px;
  flex: 1 0 auto;
  font-size: 12px;
  height: 24px;
  line-height: 24px;
}
.sidebar-content .sub-title {
  color: #888;
  font-size: 14px;
  line-height: 32px;
  margin: 0;
}
.sidebar-content .am-list-line .am-list-content {
  font-size: 14px;
}
.my-drawer {
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
